.wrapper {
  position: absolute;
  z-index: 100;

  background-color: transparent;
}

.openBtn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;

  position: absolute;
  right: 0;
  top: 0;
  cursor: url('../../assets/svg/pawCursor.svg'), auto;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #c5dff6;
  color: #54adff;

  transition: color 250ms cubic-bezier(0.215, 0.61, 0.355, 1),
    background-color 250ms cubic-bezier(0.215, 0.61, 0.355, 1);

  &:hover,
  &:focus {
    color: #ffffff;
    border: none;
    background-color: transparent;
    cursor: url('../../assets/svg/pawCursor.svg'), auto;

    &::before {
      opacity: 1;
    }
  }

  &::before {
    content: '';
    width: 100%;
    height: 100%;
    border-radius: inherit;

    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;

    background: linear-gradient(290.46deg, #419ef1 0%, #9bd0ff 107.89%);
    opacity: 0;

    transition: opacity 250ms cubic-bezier(0.215, 0.61, 0.355, 1);
  }
}

.openBtnLabel {
  display: none;
}

.openBtnIcon {
  stroke: currentColor;
}

.dropdownContainer {
  position: absolute;
  margin-top: 15px;
  padding: 8px;
  border-radius: 20px;

  background-color: #ffffff;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
}

.text {
  margin-bottom: 8px;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;

  letter-spacing: 0.04em;

  color: #54adff;
}

.submenu {
  width: 136px;
  margin-bottom: 8px;
  padding: 8px;
  padding-right: 22px;
  border-radius: 20px;
  background-color: #cce4fb;
  transition: 250ms cubic-bezier(0.215, 0.61, 0.355, 1);

  &:hover,
  &:focus {
    background-color: #aed1f3;

    & button {
      background-color: #aed1f3;
    }
  }
}

.filterBtn {
  padding: 0;
  border: none;

  display: flex;
  align-items: center;
  gap: 10px;
  background-color: #cce4fb;
  transition: 250ms cubic-bezier(0.215, 0.61, 0.355, 1);
}

.btnLabel {
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;

  color: #54adff;
}

.form {
  margin-top: 8px;
  padding-left: 18px;

  display: flex;
  flex-direction: column;
  gap: 8px;
}

.label {
  padding: 4px;
  display: flex;
  align-items: center;
  gap: 16px;

  font-family: 'Inter';

  font-weight: 400;
  font-size: 12px;
  line-height: 16px;

  color: #54adff;
}

.input {
  width: 16px;
  height: 16px;
  margin: 0;

  display: grid;
  place-content: center;

  line-height: 0;

  border-radius: 4px;
  border: 2px solid #54adff;

  appearance: none;

  &::before {
    content: '';
    width: 0.85em;
    height: 0.85em;
    transform: scale(0);

    box-shadow: inset 1em 1em #54adff;

    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
  }

  &:checked {
    &::before {
      transform: scale(1);
    }

    &:hover,
    &:focus {
      transform: scale(1.2);
    }
  }
}

@media screen and (max-width: 767px) {
  .dropdownContainer {
    // right: 0;
    // top: 0;
    // display: block;
    margin-left: auto;
    margin-right: auto;
    width: 152px;
    right: -12px;
  }
  .openBtn {
    top: -40px;
    position: initial;
  }
}

@media screen and (min-width: 768px) {
  .wrapper {
    position: static;
  }

  .openBtn {
    position: relative;
    padding: 9px;
    width: 152px;
    border: 2px solid #54adff;
    border-radius: 40px;

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 8px;

    background-color: transparent;
    cursor: pointer;
  }

  .openBtnLabel {
    display: block;
    font-weight: 700;
    font-size: 16px;
    line-height: 22px;
  }

  .dropdownContainer {
    margin-top: 20px;
  }
}
