@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.0.0-beta3/css/all.min.css');

.search-component {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 8px;
  border-radius: 5px;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
  width: 608px;
  height: 44px;
  margin: 40px auto;
  border-radius: 40px;

  &__input {
    flex: 1;
    border: none;
    background: transparent;
    outline: none;
    font-size: 16px;
    padding: 8px;
  }

  &__clear-button,
  &__search-button {
    border: none;
    background: transparent;
    cursor: pointer;
    font-size: 20px;
    padding: 8px;
    color: #88c6fd;
    cursor: url('../../assets/svg/pawCursor.svg'), auto;
  }

  @media screen and (max-width: 767px) {
    width: 280px;
    margin: 20px auto;
  }
}
